/*----------------обнуление----------------*/
*,
*::before,
*::after {
    padding: 0;
    margin: 0;
    border: 0;
    box-sizing: border-box;

}

a {
    text-decoration: none;
}

ul,
ol,
li {
    list-style: none;
}

img {
    vertical-align: top;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    font-weight: inherit;
    font-size: inherit;
}

html,
body {
    width: 100%;
    height: 100%;
    line-height: 1;
    font-size: 16px;
    color: #000;
    margin: 0 auto;
    background-color: #F2F2F2;
}

button {
    cursor: pointer;
}
